body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.form-control label,
.form-control input,
.form-control textarea {
  width: 100%;
  display: block;
}

.form-control input,
.form-control textarea {
  border: 1px solid #ccc;
}

.heading {
  text-align: center;
  font-size: 2.5rem;
  color: #6c8181;
}

.form-control {
  margin-bottom: 1.5rem;
}

.form-control label {
  margin-bottom: 0.5rem;
}

.form-actions button,
.btn {
  background: #01d1d1;
  font: inherit;
  border: 1px solid #01d1d1;
  border-radius: 3px;
  padding: 0.25rem 1rem;
  margin-right: 1rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.26);
  color: white;
  cursor: pointer;
}

.form-actions button:hover,
.form-actions button:active,
.btn:hover,
.btn:active {
  background: #01a7a7;
  border-color: #01a7a7;
}
