.mainNavigation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 3.5rem;
  background: #01d1d1;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.mainNavigation__logo h1 {
  margin: 0;
  font-size: 1.5rem;
  /* color: #dfcefc; */
}

.mainNavigation__items {
  margin-left: 1.5rem;
}

.mainNavigation__items ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}

.mainNavigation__items li {
  margin: 0 1rem;
}

.mainNavigation__items a,
.mainNavigation__items button {
  text-decoration: none;
  color: white;
  padding: 0.25rem 0.5rem;
  border: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

.mainNavigation__items a:hover,
.mainNavigation__items a:active,
.mainNavigation__items a.active,
.mainNavigation__items button:hover,
.mainNavigation__items button:active {
  color: #666;
  font-weight: bold;
}
